import axios from 'axios';

// Sign   API 
export const sendSignRequest = async (id, password, name, email) => {

  //   PHP   (   .)
  const SignCheckPHP = 'http://leedh9276.dothome.co.kr/test/Sign_chk.php';

  // FormData POST  . ID, PW
  const formData = new FormData();
  formData.append('id', id);
  formData.append('password', password);
  formData.append('name', name);
  formData.append('email', email);

  try { //   .
    const response = await axios.post(SignCheckPHP, formData);
    return response.data;
  } catch (error) { //   ,   
    console.error(error);

    if (error.response) { //   ,  
      const { data, status } = error.response;
      console.error(`Status code: ${status}`);
      console.error(`Error message: ${data.error}`);
      throw new Error(data.error);
    } else if (error.request) { //    
      console.error('No response received from server');
      throw new Error('An error occurred while logging in.');
    } else { //    
      console.error('Error in sending request:', error.message);
      throw new Error('An error occurred while logging in.');
    }
  }
};
