import React, {useState} from 'react';
import './GNB.css';

function GNB(props) {

  const [searchbox, setSearchbox] = useState(false);
  const searchboxEvent = () => {
    if (searchbox === false) {
      setSearchbox(true);
    } else {
      setSearchbox(false);
    }
  }

  return (
    <nav className='GNB'>
      <ul>
        <li></li>
        <li></li>
        <li> &middot; </li>
        <li></li>
        <li></li>
        <li onClick={searchboxEvent}>
          <img src={`${process.env.PUBLIC_URL}/images/search.svg`} alt="" />
        </li>
        <li>
          <img src={`${process.env.PUBLIC_URL}/images/cart.svg`} alt="" />
        </li>
      </ul>
    </nav>
  );
}

export default GNB;