import React, {useState, useEffect} from 'react';
import axios from 'axios'; // modify the import statement
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
import './css/MujiProduct.css';


function Product(props) {

  const [error, setError] = useState(null);

  // state
  const [productID, setProductID] = useState('');
  const [productName, setProductName] = useState('');
  const [productPrice, setProductPrice] = useState('');
  const [productImage, setProductImage] = useState('');
  const [detailImage, setDetailImage] = useState('');

  //  ?id   id  
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const id = params.get('id');

  const productListPHP = 'http://leedh9276.dothome.co.kr/test/Product_list.php';

  // useEffect      
  useEffect(() => {
    fetchList();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchList = async() => {
    try {
      const response = await axios.get(`${productListPHP}?id=${id}`);
      setProductID(response.data.no);
      setProductName(response.data.name);
      setProductPrice(response.data.price);
      setProductImage(response.data.img);
      setDetailImage(response.data.detail_imgs);
      console.log(response.data);
    } catch (e) {
      setError(e);
      console.log(e + error);
    }
  }

  return (
    <div className='list-wrap'>
      <h2></h2>
      <ul>
        <li>: {productID}</li>
        <li>: {productName}</li>
        <li>: {productPrice}</li>
        <li>: {productImage}</li>
      </ul>
      <Link to = '/'>
        <button></button>
      </Link>

      <div className="detail">
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[0]}`} alt="1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[1]}`} alt="1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[2]}`} alt="1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[3]}`} alt="1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[4]}`} alt="1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[5]}`} alt="1" />
      </div>
    </div>
  );
}

export default Product;