import React, {useState, useEffect} from 'react';
import { useNavigate, Link } from 'react-router-dom';
import LNB_wear from './LNB_wear';
import LNB_Funiture from './LNB_Funiture';
import './Header_sub.css';
import LNB_Other from './LNB_Other';

function HeaderSub({setToggleState, toggleState, userName, logOut, toggle}) {

  const [headerSub, setHeaderSub] = useState('header-sub');
  const [act, setAct] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    if (toggleState === 1) {
      setHeaderSub('header-sub active');
      setAct('Active');
    } else {
      setHeaderSub('header-sub');
      setAct('');
    }
  }, [toggleState]);

  const login = () => {
    setToggleState(0);
    navigate('/login');
  }
  const logOutBtn = () => {
    setToggleState(0);
    logOut();
  }

  const register = () => {
    console.log('');
    setToggleState(0);
    navigate('/signup');
  }

  return (
    <div className={headerSub}>
      <div className="header-sub-wrap">
        <LNB_Funiture />

        <div>
          {userName ? (
            <div className='profile-wrap'>
              <img src="http://leedh9276.dothome.co.kr/test/img/profile/profile.png" alt=" " />
              <p className='profile-info'>
                <span> <br />
                <strong>{userName}.</strong> 
                </span>
                <button onClick={logOutBtn} className='profile-btn'></button>
              </p>
              <p className='my-profile'>
                <span></span>
                <span></span>
                <span></span>
              </p>
            </div>
          ) : (
            <div className='profile-wrap'>
              <img src="http://leedh9276.dothome.co.kr/test/img/profile/defaultprofile.png" alt=" " />
              <p className='profile-info'>
                <span>   . <br />
                <strong></strong> .
                </span>
              </p>
              <p className='profile-info'>
                <button onClick={login} className='login-btn'></button>
                <button onClick={register} className='join-btn'></button>
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default HeaderSub;